import {
  NgModule
} from '@angular/core';
import {
  Routes,
  RouterModule
} from '@angular/router';
import {
  ReportsComponent
} from './reports/reports.component';
import {
  PaymentSettingComponent
} from './payment-setting/payment-setting.component';
import {
  PaymentHistoryComponent
} from './payment-history/payment-history.component';
import {
  LoginDetailsComponent
} from './login-details/login-details.component';
import {
  FaqComponent
} from './faq/faq.component';
import {
  MynavComponent
} from './mynav/mynav.component';
import {
  PagenotfoundComponent
} from './pagenotfound/pagenotfound.component';
import {
  SigninComponent
} from './signin/signin.component';
import {
  SignupComponent
} from './signup/signup.component';
const routes: Routes = [{
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'login',
    component: SigninComponent
  },
  {
    path:'signup',
    component:SignupComponent
  },
  {
    path: 'home',
    component: MynavComponent,
    children: [{
        path: '',
        redirectTo: 'report',
        pathMatch: 'full'
      },
      {
        path: 'report',
        component: ReportsComponent
      },
      {
        path: 'payment-setting',
        component: PaymentSettingComponent
      },
      {
        path: 'payment-history',
        component: PaymentHistoryComponent
      },
      {
        path: 'login-details',
        component: LoginDetailsComponent
      },
      {
        path: 'faq',
        component: FaqComponent
      }
    ]
  },
  // {path: '', redirectTo: 'signin', pathMatch: 'full'},
  {
    path: '**',
    component: PagenotfoundComponent
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}

export const routingComponents = [ReportsComponent, PaymentSettingComponent, SigninComponent,SignupComponent,
  LoginDetailsComponent, PaymentHistoryComponent, FaqComponent, MynavComponent, PagenotfoundComponent
];
